import { createRouter, createWebHashHistory } from 'vue-router';

const title = 'ChillBar';

const routes = [
  {
    path: '/',
    name: 'index',
    component: () => import('@/views/frontEnd/FrontEndView.vue'),
    children: [
      {
        path: '/',
        name: 'home',
        component: () => import('@/views/frontEnd/HomeView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'product',
        name: 'product',
        component: () => import('@/views/frontEnd/ProductView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'detail/:id',
        name: 'product-detail',
        component: () => import('@/views/frontEnd/ProductDetailView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'booking',
        name: 'booking',
        component: () => import('@/views/frontEnd/BookingView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'login',
        name: 'login',
        component: () => import('@/views/frontEnd/LoginView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'checkout',
        name: 'check-order',
        component: () => import('@/views/frontEnd/CheckOrderView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'confirm',
        name: 'order-confirm',
        component: () => import('@/views/frontEnd/OrderConfirmView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
      {
        path: 'about',
        name: 'about',
        component: () => import('@/views/frontEnd/AboutUsView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
    ],
  },
  {
    path: '/admin',
    name: 'Admin',
    component: () => import('@/views/backEnd/BackEndView.vue'),
    children: [
      {
        path: '',
        name: 'dashboard',
        component: () => import('@/views/backEnd/DashboardView.vue'),
        meta: {
          title: `${title} - `,
        },
      },
    ],
  },
  {
    path: '/:pathMatch(.*)*',
    name: '404',
    component: () => import('@/views/frontEnd/The404View.vue'),
    meta: {
      title,
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    return {
      top: 0,
    };
  },
});

export default router;
